.placeholder {
  height: 500px;
  background: lightblue; }

.square {
  width: 200px;
  height: 200px;
  background: blue; }

.que {
  display: flex; }

.wrapper {
  background: #e08f41;
  height: 300vh; }

.mainHeader {
  background: #54817c;
  color: white;
  font-size: 60px;
  display: flex;
  padding: 20px;
  justify-content: center; }

.scene {
  height: 100vh;
  border-bottom: 2px dashed #54817c; }

.scene:first-child {
  display: flex;
  align-items: center;
  justify-content: center; }

.scene:nth-child(2) {
  display: flex;
  align-items: center;
  justify-content: center; }

.scene:nth-child(3) {
  display: flex;
  align-items: center;
  justify-content: center; }
  .scene:nth-child(3) img {
    width: 100%;
    height: 100%;
    opacity: 0; }

.eye {
  width: 250px;
  height: 250px;
  background: lightgreen;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }
